.App {
  text-align: center;
  padding: 130px 0 0;
}

.Quote-box {
  max-width: 500px;
  border-radius: 12px;
  padding: 50px 70px 40px;
  margin: 0 auto ;
  color: hsl(0, 94%, 79%);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.75);
  border: 1px solid rgba(209, 213, 219, 0.3);
}

/* .Text {
  color: lightsalmon;
} */

.Text h1 {
  margin-top: 0;
}

.petik:before {
  font-size: 50px;
  margin: 0;
  content: '\201C';
  color: #17b585;
}

.Author {
  /* color: lightsalmon; */
  text-align: right;
}

.Menu {
  display: flex;
}

.ButtonNewQuote {
  font-size: 16px;
  padding: 15px 50px;
  margin-top: 10px;
  border: none;
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.75);
  background-color: #50E3C2;
  background-image: 
    radial-gradient(at 47% 33%, hsl(162.00, 77%, 40%) 0, transparent 59%), 
    radial-gradient(at 82% 65%, hsl(0, 94%, 79%), transparent 95%);
  cursor: pointer;
}

.LinkTweetQuote {
  position: absolute;
  left: 50%;
  top: 50px;
  padding: 15px;
  z-index: 1;
}

.tweet-quote {
  position: relative;
  left: -50%;
}

svg {
  fill: #55acee;
}

svg:hover, svg:focus {
  fill: rgba(255, 255, 255, 0.75);
  padding: 10px;
  border-radius: 12px;
  background-color: #50E3C2;
  background-image: 
    radial-gradient(at 47% 33%, hsl(162.00, 77%, 40%) 0, transparent 59%), 
    radial-gradient(at 82% 65%, hsl(0, 94%, 79%), transparent 95%); 
}